#root,
body,
html {
  color: #000;
  font-size: 16px;
}

h1 {
  font-size: 2rem;
}

h2 {
  font-size: 1.5rem;
}

h3 {
  font-size: 1.2rem;
  margin-bottom: 1rem;
}

h4 {
  margin: 0 0 0.25rem;
  padding: 0;
}

p {
  font-size: 1rem;
  line-height: 1.5rem;
  margin: 0 0 1rem;
}

body {
  background: #fff;
}

body.blueToPink {
  background: #559ad4;
  background: -webkit-linear-gradient(to top, #e27191, #ae84d4, #559ad4);
  background: linear-gradient(to top, #e27191, #ae84d4, #559ad4);
}

body.pinkToPurple {
  background: #bc4e9c;
  background: -webkit-linear-gradient(to top, #ff3479, #bc4e9c);
  background: linear-gradient(to top, #ff3479, #bc4e9c);
}

body.rainbow {
  background: #40E0D0;
  background: -webkit-linear-gradient(to top, #FF0080, #FF8C00, #40E0D0);
  background: linear-gradient(to top, #FF0080, #FF8C00, #40E0D0);
}

body.sandToNight {
  background: #3E5151;
  background: -webkit-linear-gradient(to bottom, #DECBA4, #3E5151);
  background: linear-gradient(to bottom, #DECBA4, #3E5151);
}

body.greenToLightGreen {
  background: #11998e;
  background: -webkit-linear-gradient(to top, #38ef7d, #11998e);
  background: linear-gradient(to top, #38ef7d, #11998e);
}

body.orangeToBlue {
  background: #0d9de0;
  background: -webkit-linear-gradient(to bottom, #ff973b, #0d9de0);
  background: linear-gradient(to bottom, #ff973b, #0d9de0);
}

body.redToDarkPurple {
  background: #ff4d4d;
  background: -webkit-linear-gradient(to top, #732179, #ff4d4d);
  background: linear-gradient(to top, #732179, #ff4d4d);
}

body.whiteToRed {
  background: #fffbd5;
  background: -webkit-linear-gradient(to top, #b20a2c, #fffbd5);
  background: linear-gradient(to top, #b20a2c, #fffbd5);
}

body.lightBlueToLightGreen {
  background: #74ebd5;
  background: -webkit-linear-gradient(to bottom, #ACB6E5, #74ebd5);
  background: linear-gradient(to bottom, #ACB6E5, #74ebd5);
}

body.lightPinkRainbow {
  background: #03001e;
  background: -webkit-linear-gradient(to bottom, #fdeff9, #ec38bc, #8733c1, #5845ff);
  background: linear-gradient(to bottom, #fdeff9, #ec38bc, #8733c1, #5845ff);
}

body.sandToRed {
  background: #e1eec3;
  background: -webkit-linear-gradient(to top, #f05053, #e1eec3);
  background: linear-gradient(to top, #f05053, #e1eec3);
}

body.yellowToBlue {
  background: #1a2a6c;
  background: -webkit-linear-gradient(to bottom, #fdbb2d, #c51c1c, #1d3ec9);
  background: linear-gradient(to bottom, #fdbb2d, #c51c1c, #1d3ec9);
}

body.greenToYellow {
  background: #22c1c3;
  background: -webkit-linear-gradient(to top, #fdbb2d, #22c1c3);
  background: linear-gradient(to top, #fdbb2d, #22c1c3);
}

.Page {
  margin-top: 4rem;
  margin-bottom: 4rem;
}

.Header {
  display: flex;
  justify-content: space-between;
  margin: 1.5rem 2rem 4rem;
}

.Header > h1 {
  margin: 0;
  padding: 0;
}

.Header__connected {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.Header__connected > span:first-child {
  font-weight: bold;
  display: flex;
  align-items: center;
}

.Header__connected > span:last-child {
  display: block;
  margin-top: 0.25rem;
}

.Header__connected > svg {
  margin-right: 0.25rem;
}

.Body__section {
  margin-bottom: 3rem;
}

.Body__section > h2 {
  text-align: center; 
}

.Body__section a {
  color: inherit;
}

.Body__about > h2 {
  margin-bottom: 2rem;
}

.Body__connection {
  text-align: center;
  margin-bottom: 4rem;
}

.Message--error {
  align-items: flex-start;
  color: #d32f2f;
  display: flex;
  margin-top: 1rem;
}

.Message--error > svg {
  padding-top: 2px;
}

.Message--error > p {
  font-size: 0.875rem;
  line-height: 1.4rem;
  margin-left: 0.3rem;
  margin-bottom: 0;
}

.Step__action {
  background: #f1f1f1;
  margin-top: 1.5rem;
  padding: 2rem;
}

.Step__action > h3 {
  margin:  0 0 1rem;
}

.Step__message {
  border-radius: 3px;
  font-size: 0.875rem;
  margin-top: 1rem;
  padding: 0.8rem 1rem 0.70rem;
}

.Step__message.success {
  background-color: rgba(29, 233, 182, 0.1);
  border: 2px solid #1de9b6;
}

.Step__message.pending {
  background-color: rgba(255, 239, 98, 0.15);
  border: 2px solid #ffef62;
}

.Step__message.confirmed {
  background-color: rgba(205, 205, 205, 0.2);
  border: 2px solid #cdcdcd;
}

.Step__message.failed {
  background-color: rgba(238, 68, 68, 0.1);
  border: 2px solid #ee4444;
}

.Step__message > h4 {
  font-size: 0.875rem;
}

.Step__message p {
  font-size: inherit;
  margin: 0;
}

.Step__message a {
  color: inherit;
  font-size: inherit;
}

.Step__message > p {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.Step__message-line {
  display: flex;
  justify-content: space-between;
  white-space: nowrap;
}

.Step__message-line > a {
  display: block;
  flex-shrink: 0;
}

.Step__message-line > span {
  color: #454545;
  display: block;
  flex-shrink: 1;
  overflow: hidden;
  padding-top: 2px;
  margin: 0 3px;
}

.OrderForm {
  margin-top:  2rem;
}

.OrderForm__NFTProjectSelect {
  width: 100%;
}

.OrderForm__fields {
}

.OrderForm__fields > div {
  margin-bottom: 1.5rem;
  width: 100%;
}

.OrderForm__fields-description {
  margin:  1rem 0 0;
}

.OrderForm__general-error {
  color: red;
  font-weight: bold;
  margin-top: 1.25rem;
}

.OrderForm__submit { 
  display: flex;
  justify-content:  flex-end;
  margin-top: 1.5rem;
}

.Section__pending > .Spinner {
  color: inherit;
}

.Section__pending,
.Section__error,
.Section__not-connected,
.Section__none {
  padding: 2rem 1rem;
}

.Section__error > p,
.Section__not-connected > p,
.Section__none > p {
  color: #8f8f8f;
  font-size: 1rem;
  margin: 0;
  text-align: center;
}

.Section__list,
.Section__list-container {
  list-style: none;
  margin: 1.5rem 0 0;
  max-height: 214px;
  overflow: scroll;
  padding: 0;
  text-align: left;
}

.Section__list > li {
  margin-bottom: 0.75rem;
}

.Section__list > li.disabled {
  pointer-events: none;
}

.Section__list > li.disabled > span {
  text-decoration: none;
}

.Section__list > li:last-child {
  margin-bottom: 0;
}

.Section__list > li > span > .Spinner {
  color: inherit;
  display: inline-block;
  min-height: 14px;
}

.Section__list > li > span > .Spinner > div {
  width: 14px !important;
  height: 14px !important;
}

.Section__list > li > span,
.Section__sublist > li > span { 
  cursor: pointer;
  font-size: 0.9rem;
  margin-left: 0.5rem;
  text-decoration: underline;
}

.Section__sublist-container:not(:last-child) {
  margin-bottom: 1rem;
}

.Section__sublist-container > h4 {
  border-bottom: 1px solid lightgray;
  padding: 0 0 0.5rem;
  margin: 0 0 1rem;
}

.Section__sublist {
  color: gray;
  font-size: 0.9rem;
  line-height: 1.4rem;
  list-style: none;
  padding: 0 0 0 1rem;
  margin: 0;
}

.AllOpenOrders__list {
  list-style: none;
  margin: 1.5rem 0 0;
  padding: 0;
  text-align: left;
  max-height: 218px;
  overflow: scroll;
}

.AllOpenOrders__list > li {
  margin-bottom: 0.75rem;
}

.AllOpenOrders__list > li:last-child {
  margin-bottom: 0;
}

.AllOpenOrders__list > li > span {
  font-weight: bold;
}

.Body__faq {
  margin-top: 6rem;
}

.PendingTxs {
  position: fixed;
  bottom: 1rem;
  right: 1rem;
}

.PendingTx {
  align-items: flex-start;
  background-color: #262726;
  border-radius: 6px;
  color: #e8edeb;
  display: flex;
  line-height: 1rem;
  margin-top: 1rem;
  padding: 1rem 1.5rem 1rem 1.3rem;
  width: 320px;
}

.PendingTx a {
  color: inherit;
  text-decoration: none;
}

.PendingTx > div {
  margin-top: 2px;
}

.PendingTx > div > h4 {
  display: inline-block;
  font-weight: bold;
  margin: 1px 0 0.4rem;
}

.PendingTx > div > h5 {
  font-size: 0.95rem;
  font-weight: normal;
  line-height: 1.35rem;
  margin: 0;
}

.FAQ__list {
  list-style: none;
  padding: 0;
  margin: 2rem 0 0;
}

.FAQ__list > dt {
  font-weight: bold;
  padding: 0;
  margin: 0 0 0.5rem;
  line-height: 1.5rem;
}

.FAQ__list > dd {
  padding: 0;
  margin: 0 0 2rem;
  line-height: 1.5rem;
}


